@use '../util/' as u;

html {
	box-sizing: border-box;
	font-size: 100%;
}
*,
*::before,
*::after {
	box-sizing: inherit;
}
body {
	margin: 0;
	padding: 0;
	font-family: u.$font;
	background-color: var(--background-color);
	color: var(--text-dark);
}
img {
	display: block;
	width: 100%;
	height: auto;
}
.visually-hidden {
	position: absolute;
	left: -10000px;
	top: auto;
	width: 1px;
	height: 1px;
	overflow: hidden;

	&:focus {
		left: 0;
		width: auto;
		height: auto;
		background: #fff;
		padding: 0.5em;
		border: 1px solid;
	}
}
