@use '../util/' as u;

.topnav {
	background-color: var(--header-bg);

	&__wrapper {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-block: 12px;

		@include u.breakpoint(large) {
			padding-block: 20px;
		}
	}

	&__homelink {
		transition: opacity 0.4s;

		&:hover {
			opacity: 0.8;
		}
	}

	&__logo {
		width: u.rem(70);

		@include u.breakpoint(large) {
			width: u.rem(128);
		}
	}

	&__links {
		display: flex;
		gap: 20px;
		margin: 0;
		padding: 0;

		@include u.breakpoint(large) {
			gap: 40px;
		}
	}

	&__item {
		list-style-type: none;
	}

	&__link {
		font-size: u.rem(14);
		font-weight: 700;
		color: var(--text-light);
		text-transform: uppercase;
		letter-spacing: 0.05em;

		&:hover {
			text-decoration: underline;
			text-decoration-thickness: 2px;
			text-underline-offset: 4px;
		}

		@include u.breakpoint(large) {
			font-size: u.rem(16);
		}
	}
}
