@use '../util/' as u;

h1,
h2,
h3 {
	margin-top: 0;
	font-weight: 700;
}

h1 {
	font-size: 2.625rem;
	font-size: clamp(u.rem(42), u.rem(28) + 3.64vw, u.rem(72));
	line-height: 1;
	margin-block-end: u.rem(20);

	// @include u.breakpoint(medium) {
	// 	font-size: u.rem(60);
	// }

	// @include u.breakpoint(large) {
	// 	font-size: u.rem(72);
	// }
}

h2 {
	font-size: u.rem(36);
	font-size: clamp(u.rem(36), 1.91rem + 1.45vw, u.rem(48));
}

h3 {
	font-size: u.rem(24);
	line-height: 1;
}

p {
	font-size: u.rem(18);
	margin-top: 0;
	line-height: 1.3;

	&.medium {
		font-size: u.rem(20);
		font-size: clamp(u.rem(20), 1.14rem + 0.48vw, u.rem(24));
	}

	&.large {
		font-size: u.rem(24);
		font-size: clamp(u.rem(24), 1.39rem + 0.48vw, u.rem(28));
		margin-block-end: u.rem(40);
	}
}
a,
a:visited,
a:active {
	text-decoration: none;
}
